import React from "react"

class HeaderComponent extends React.Component{
    render(){
        return (
            <header>
                <nav class="navbar navbar-expand-md navbar-dark sticky-top" style={{backgroundColor : "#0E242D"}}>
                    <div class="container-fluid">
                        <a href="#" class="navbar-brand"><img alt="logo" style={{maxHeight : "70px"}} src="images/site-logo.png"></img></a>
                        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarResponsive">
                            <span class="navbar-toggler-icon"> </span>
                        </button>
                        <div class="collapse navbar-collapse" id="navbarResponsive">
                            <ul class="navbar-nav ml-auto">
                                <li class="nav-item">
                                    <a class="nav-link" href="#">Home</a>
                                </li>
                                <li class="nav-item">
                                    <a class="nav-link" href="#">About</a>
                                </li>
                                <li class="nav-item">
                                    <a class="nav-link" href="#">Services</a>
                                </li>
                                <li class="nav-item">
                                    <a class="nav-link" href="#">Samples</a>
                                </li>
                                <li class="nav-item">
                                    <a class="nav-link" href="#">Contact</a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </nav>
            </header>
        )
    }
}

export default HeaderComponent